nav{
    min-height: 10vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    // background-color: wheat;
    width: 100%;
    button{
        background: transparent;
        border: none;
        cursor: pointer;
        border: 2px solid rgb(65, 65, 65);
        padding: 0.5rem;
        transition: all 0.3s ease;
        &:hover{
            background: rgb(65, 65, 65);
            color: white;
        }
    }
}
@media screen and (max-width:768px) {
    nav{
         .btn{
            z-index: 10;
         }
    }
}